body {
  margin: 0;
  background-color: #d9d9d9;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Header starts*/
.header, .header-ul {
  display: flex;
  flex-direction: row;
  align-items: center;
  text-align: center;
}

.header-ul {
  margin: 1rem;
}

.nav {
  background-color: #d9d9d9;
  display: flex;
  /*flex-direction: row;*/
  align-items: center ;
  color: #947f57;
  padding: 0.5rem;
  margin-right: 10rem;
  font-size: 1.5rem;
}
.nav:hover {
  background-color: #947f57;
  color: #d9d9d9;
  box-shadow:5px 10px 8px 0px #888888;
  border-radius: 1rem;
  transition-duration: 0.5s;
}

.navActive {
  background-color: #947f57;
  color: #d9d9d9;
  box-shadow:5px 5px 8px 0px #888888;
  border-radius: 1rem;
}

.nav:first-child {
  background-color: #d9d9d9;
  color: #947f57;
  box-shadow: none;
  border-radius: 100%;
  border-style:double;
  margin-left: 2rem;
  margin-right: 15rem;
}
/* Header ends*/

/* Home page starts*/

#logo-image {
  margin: auto;
}

.home-page {
  display: flex;
  flex-direction: row;
  align-items: center;
  text-align: center;
}

/*#header-logo {
  margin-top:0;
  padding-left: 2%;
}*/

/* Home page ends*/

/* Conatct page ends*/
.contact-page {
  background-color: #947f57;
  background-image: linear-gradient(#947f57, white);
  width:fit-content;
  color:#452E0C;
  border-radius: 1rem;
}
.contact-page input, .contact-page textarea, .contact-page button {
  border-radius: 1rem;
  margin:1rem;
}

.contact-button {
  text-decoration: none;
  color:#d9d9d9;
  background-color: #947f57;
  padding: 0.5rem;
  border-radius: 0.5rem;
}

.contact-button:hover {
  background-color: #d9d9d9;
  color: #947f57;
  box-shadow:5px 10px 8px 0px #888888;
  border-radius: 1rem;
  transition-duration: 1s;
}
/* Contact page ends*/

/* Resume page starts*/
.resume-page {
  text-align: left;
  margin: 1rem;
  background-color: #947f57;
  background-image: linear-gradient(#947f57, white);
  width:fit-content;
  color:#452E0C;
  border-radius: 1rem;
  padding:2rem;
}

.resume-page h2 {
  text-align: center;
}

#list-proficiencies {
  text-align: left;
}

#resume {
  background-color: #947f57;
  padding: 0.5rem;
  padding-left: 0;
  border-radius: 0.5rem;
  margin:auto;
  text-align: center;
}

#resume:hover {
  background-color: #d9d9d9;
  color: #947f57;
  box-shadow:5px 10px 8px 0px #888888;
  border-radius: 1rem;
  transition-duration: 1s;
}

#resume-link {
  text-decoration: none;
  color:#452E0C;
}
/* Resume page ends*/

/* Portfolio page starts*/
.portfolio-page {
  padding-top:0;
  margin: 1rem 1rem;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;

  background-color: #947f57;
  background-image: linear-gradient(#947f57, white);
  width:fit-content;
  color:#452E0C;
  border-radius: 1rem;
  padding:2rem;
}

.transparent {
  color:#d9d9d9;
}
.card {
  width: 80%;
  max-height: 100vh;
  text-align: center;
  /* background-color: #947f57; */
  background-color:#d9d9d9;
  border: none;
  margin: 20px;
  box-shadow:5px 10px 8px 0px #d9d9d9;
}

.card:hover .card-img {
  opacity: 0.1;
  transition: all 0.2s; 
}

.card:hover {
  background-color: #35312a; 
  color:#d9d9d9;
}

.card .card-title,
.card .card-text {
  display: none;
  
}

.card:hover .card-title,
.card:hover .card-text {
  display: contents;
  font-size: 15px;
  /* padding: 0.75 rem; */
}

.card-title,
.card-text {
  color: #452E0C;
  /* padding: 0.75 rem; */
}

.card-title:hover,
.card-text:hover {
  text-decoration: none;
  color: #452E0C;
}

.card i {
  margin: 8px;
}

#github-portfolio{
  padding: 0.75rem;
  font-size: 30px;
  border-radius: 0.25rem;
}

#github-portfolio:hover {
  box-shadow:5px 5px 5px 5px #d9d9d9;
  transition-duration: 1s;
  /* color: #452E0C; */
}

.fa-link {
  padding: 0.25rem;
  font-size: 30px;
  border-radius: 0.25rem;
  color: #d9d9d9;
}

.fa-link:hover {
  box-shadow:5px 5px 5px 5px #d9d9d9;
  transition-duration: 1s;
  /* color: #452E0C; */
}

#project-description {
  font-size: 1rem;
}
/* Portfolio page ends*/

/* About page starts*/
.about-page {
  text-align: justify;
}
/* About page ends*/

/*Media queries*/

@media screen and (max-width: 980px) {

  /*Header*/
  .header, .header-ul {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  
  .header-ul {
    margin: 1rem;
    align-items: center;
    text-align: center;
  }
  .nav {
    margin-bottom: 1rem;
  }
  .nav:first-child {
  
  margin-left: 5rem;
}
  
  /*Portfolio page*/
  .fa-link {
    padding: 0.25rem;
    font-size: 20px;
    border-radius: 0.25rem;
  }

  .card {
    width: 90%;
    margin-bottom: 10rem;
    justify-content: space-between;
  }
}
